import React, { useState } from "react";
import data from "./data";
import Person from "./Person";

function List() {
  const [people, setPeople] = useState(data);

  function clearPeople() {
    setPeople([]);
  }

  return (
    <div>
      <Person people={people} />
      <button
        onClick={clearPeople}
        type="button"
        className="btn mt-5 btn-primary"
      >
        Clear All
      </button>
    </div>
  );
}

export default List;
