import React from "react";

function Person(props) {
  const { people } = props;
  return (
    <div>
      {people.map((person) => {
        return (
          <div key={person.id} className="card my-4" style={{ width: "18rem" }}>
            <img
              className="card-img-top"
              src={person.image}
              alt={person.name}
            />
            <div className="card-body">
              <h5>{person.name}</h5>
              <p>{person.age} years</p>
            </div>
          </div>
        );
      })}
    </div>
  );
}
export default Person;
